<h1 id="mysql-installing" tabindex="-1"><a class="header-anchor" href="#mysql-installing" aria-hidden="true">#</a> MySQL Installing</h1>
<ul>
<li><a href="https://dev.mysql.com/downloads/" target="_blank" rel="noopener noreferrer">MySQL Community Downloads<OutboundLink/></a></li>
<li><a href="https://dev.mysql.com/doc/refman/8.0/en/installing.html" target="_blank" rel="noopener noreferrer">DOCUMENTATION<OutboundLink/></a></li>
</ul>
<h2 id="add-firewalld-port" tabindex="-1"><a class="header-anchor" href="#add-firewalld-port" aria-hidden="true">#</a> Add firewalld port</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>firewall-cmd --zone<span class="token operator">=</span>public --add-port<span class="token operator">=</span><span class="token number">3306</span>/tcp --permanent 
firewall-cmd --reload
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><h2 id="linux-yum-repository" tabindex="-1"><a class="header-anchor" href="#linux-yum-repository" aria-hidden="true">#</a> Linux Yum Repository</h2>
<ul>
<li><a href="https://dev.mysql.com/downloads/repo/yum/" target="_blank" rel="noopener noreferrer">下载地址<OutboundLink/></a></li>
<li><a href="https://dev.mysql.com/doc/refman/8.0/en/linux-installation-yum-repo.html" target="_blank" rel="noopener noreferrer">官方文档<OutboundLink/></a></li>
</ul>
<h3 id="adding-the-mysql-yum-repository" tabindex="-1"><a class="header-anchor" href="#adding-the-mysql-yum-repository" aria-hidden="true">#</a> Adding the MySQL Yum Repository</h3>
<ol>
<li>Remove MARIADB</li>
</ol>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">rpm</span> -qa <span class="token operator">|</span> <span class="token function">grep</span> mariadb
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> yum -y remove mariadb-libs-5.5.68-1.el7.x86_64
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ol start="2">
<li>Adding the MySQL Yum Repository</li>
</ol>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">wget</span> https://dev.mysql.com/get/mysql80-community-release-el7-3.noarch.rpm
<span class="token function">sudo</span> yum <span class="token function">install</span> -y mysql80-community-release-el7-3.noarch.rpm
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><ol start="3">
<li>check that the MySQL Yum repository has been successfully</li>
</ol>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>yum repolist enabled <span class="token operator">|</span> <span class="token function">grep</span> <span class="token string">"mysql.*-community.*"</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="selecting-a-release-series" tabindex="-1"><a class="header-anchor" href="#selecting-a-release-series" aria-hidden="true">#</a> Selecting a Release Series</h3>
<ol>
<li>See all the subrepositories in the MySQL Yum repository</li>
</ol>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>yum repolist all <span class="token operator">|</span> <span class="token function">grep</span> mysql
yum repolist enabled <span class="token operator">|</span> <span class="token function">grep</span> mysql
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><p>When using the MySQL Yum repository, <strong>the latest GA series (currently MySQL 8.0) is selected for installation by default</strong>.</p>
<ol start="2">
<li>Change the Release Series</li>
</ol>
<p>such as disable the subrepository for the 5.7 series and enable the one for the 8.0 series:</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> yum-config-manager --disable mysql57-community
<span class="token function">sudo</span> yum-config-manager --enable mysql80-community
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><p>Besides using yum-config-manager or the dnf config-manager command, you can also select a release series by editing manually the <code>/etc/yum.repos.d/mysql-community.repo</code> file.</p>
<p><strong>change the enabled=1 or 0</strong></p>
<div class="language-yaml ext-yml line-numbers-mode"><pre v-pre class="language-yaml"><code><span class="token comment"># Enable to use MySQL 8.0</span>
<span class="token punctuation">[</span>mysql80<span class="token punctuation">-</span>community<span class="token punctuation">]</span>
name=MySQL 8.0 Community Server
baseurl=http<span class="token punctuation">:</span>//repo.mysql.com/yum/mysql<span class="token punctuation">-</span>8.0<span class="token punctuation">-</span>community/el/6/$basearch/
enabled=1
gpgcheck=1
gpgkey=file<span class="token punctuation">:</span>///etc/pki/rpm<span class="token punctuation">-</span>gpg/RPM<span class="token punctuation">-</span>GPG<span class="token punctuation">-</span>KEY<span class="token punctuation">-</span>mysql
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div><h3 id="disabling-the-default-mysql-module" tabindex="-1"><a class="header-anchor" href="#disabling-the-default-mysql-module" aria-hidden="true">#</a> Disabling the Default MySQL Module</h3>
<p>RHEL8 and Oracle Linux 8 include a MySQL module that is enabled by default. <strong>(EL8 systems only)</strong></p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> yum module disable mysql
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="installing-and-starting" tabindex="-1"><a class="header-anchor" href="#installing-and-starting" aria-hidden="true">#</a> Installing and Starting</h3>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> yum -y <span class="token function">install</span> mysql-community-server
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>This installs the package for MySQL server (<strong>mysql-community-server</strong>) and also packages for the components required to run the server, including packages for the client (<strong>mysql-community-client</strong>), the common error messages and character sets for client and server (<strong>mysql-community-common</strong>), and the shared client libraries (<strong>mysql-community-libs</strong>).</p>
<p>Start the MySQL server with the following command:</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>systemctl start mysqld
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>commands such as:</p>
<ul>
<li>stop</li>
<li>start</li>
<li>status</li>
<li>restart</li>
</ul>
<h3 id="find-and-reset-super-password" tabindex="-1"><a class="header-anchor" href="#find-and-reset-super-password" aria-hidden="true">#</a> Find and reset super password</h3>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> <span class="token function">grep</span> <span class="token string">'temporary password'</span> /var/log/mysqld.log
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>mysql -u root -p
ALTER <span class="token environment constant">USER</span> <span class="token string">'root'</span>@<span class="token string">'localhost'</span> IDENTIFIED BY <span class="token string">'MyNewPass4!'</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><h3 id="modify-remote-connection" tabindex="-1"><a class="header-anchor" href="#modify-remote-connection" aria-hidden="true">#</a> Modify Remote Connection</h3>
<div class="language-sql ext-sql line-numbers-mode"><pre v-pre class="language-sql"><code><span class="token keyword">update</span> <span class="token keyword">user</span> <span class="token keyword">set</span> Host <span class="token operator">=</span> <span class="token string">'%'</span> <span class="token keyword">where</span> <span class="token keyword">User</span> <span class="token operator">=</span> <span class="token string">'root'</span>
flush <span class="token keyword">privileges</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div>